<template>
  <div ref="chart" class="chart"></div>
</template>

<script>
import * as echarts from "echarts";

export default {
  name: "PieChart",
  props: {
    data: Object, //  
  },
  mounted() {
    this.initChart();
    window.addEventListener("resize", this.resizeChart); // 
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.resizeChart); //  
    this.destroyChart();
  },
  methods: {
    initChart() {
      if (!this.data || !this.$refs.chart) return;

      // ---    (Top N  ) ---
      const categories = Object.keys(this.data.SHAP);
      const seriesData = categories.map((key) => ({
        name: key,
        value: this.data.SHAP[key],
      }));
      //      Top 15 
      const topData = seriesData
          .sort((a, b) => b.value - a.value) //  
          .slice(0, 15); // Top 15 

      this.chart = echarts.init(this.$refs.chart);

      // ---   ---
      const option = {
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b}: {c} ({d}%)",
        },
        legend: {
          orient: "vertical", //    
          left: "right", //   
          data: topData.map((item) => item.name), //  
        },
        series: [
          {
            name: "SHAP Values", //  
            type: "pie",
            radius: ["30%", "60%"], //    ( Donut)
            roseType: "radius", //    
            data: topData, //  
            label: {
              show: true, //   
              formatter: "{b}: {d}%", //   
            },
            labelLine: {
              length: 15, //   
              length2: 10, //  
            },
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)", //    
              },
            },
          },
        ],
        color: [
          "#3498db",
          "#e74c3c",
          "#2ecc71",
          "#9b59b6",
          "#f39c12",
          "#1abc9c",
          "#34495e",
          "#d35400",
          "#7f8c8d",
          "#8e44ad",
          "#27ae60",
          "#c0392b",
          "#2980b9",
          "#e67e22",
          "#bdc3c7",
        ], //   
      };

      // ---   ---
      this.chart.setOption(option);
    },
    resizeChart() {
      if (this.chart) this.chart.resize();
    },
    destroyChart() {
      if (this.chart) {
        this.chart.dispose();
        this.chart = null;
      }
    },
  },
};
</script>

<style>
.chart {
  width: 100%;
  height: 100%;
}
</style>
