<template>
  <div class="feature-container" :title="feature">
    <!--      -->
    <div class="circle-container" :style="circleBackgroundStyle">
      <svg class="progress-ring" width="80" height="80">
        <!--   -->
        <circle
          class="progress-ring__background"
          r="35"
          cx="40"
          cy="40"
        />
        <!--   -->
        <circle
          class="progress-ring__progress"
          r="35"
          cx="40"
          cy="40"
          :style="progressStyle"
        />
      </svg>
      <!--   -->
      <div class="circle-text">{{ Math.abs(value).toFixed(1) }}%</div>
    </div>
    <!--   -->
    <div class="feature-name" :title="feature">
      {{ formatFeature(feature) }}
    </div>
  </div>
</template>

<script>
export default {
  name: "FeatureCircle",
  props: {
    feature: {
      type: String,
      required: true,
    },
    value: {
      type: Number,
      required: true,
    },
  },
  computed: {
    //    
    progressStyle() {
      const radius = 35; //  
      const circumference = 2 * Math.PI * radius; //  
      const absoluteValue = Math.abs(this.value); //  
      const offset = circumference - (absoluteValue / 100) * circumference; //  
      const color = this.getColor(this.value); //    

      return {
        strokeDasharray: `${circumference} ${circumference}`, //  
        strokeDashoffset: offset, //   
        stroke: color, //  
        transition: "stroke-dashoffset 0.8s ease, stroke 0.8s ease", //  
      };
    },
    //     
    circleBackgroundStyle() {
      const color = this.getColor(this.value, true); //    
      return {
        backgroundColor: color, //  
        borderRadius: "50%", // 
        width: "80px",
        height: "80px",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        transition: "background-color 0.8s ease", //     
      };
    },
  },
  methods: {
    formatFeature(feature) {
      //   feature  
      if (!feature || typeof feature !== "string") {
        return ""; //      ,   
      }
      return feature
        .replace(/_/g, " ") //  "_" 
        .replace(/\b[a-z]/g, (char) => char.toUpperCase()); //     
    },
    getColor(value, faded = false) {
      //     /
      let baseColor;
      if (value < 0) {
        //  =      
        const greenStrength = Math.min(100, Math.abs(value) * 5); //   100
        baseColor = `hsl(120, ${greenStrength}%, 50%)`; //   
      } else {
        //  =      
        const redStrength = Math.min(100, value * 5); //   100
        baseColor = `hsl(0, ${redStrength}%, 50%)`; //   
      }

      //    ,    
      if (faded) {
        return baseColor.replace("hsl", "hsla").replace(")", ", 0.2)"); //  20%
      }
      return baseColor;
    },
  },
};
</script>

<style scoped>
/*   */
.feature-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 100px; /*   */
  font-family: Arial, sans-serif;
  gap: 10px; /*  */
  padding: 10px; /*   */
  background-color: #f1f1f1; /*    */
  border-radius: 8px; /*   */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /*   */
  overflow: hidden; /*    */
  position: relative; /*  Tooltip */
}

.feature-container:hover {
  box-shadow: 0 6px 8px rgba(0, 0, 0, 0.2); /*    */
}

/*  */
.circle-container {
  position: relative;
  width: 80px;
  height: 80px;
}

/*   */
.progress-ring__background {
  stroke: #ffffff;
  stroke-width: 8; /*  */
  fill: none;
}

/*   */
.progress-ring__progress {
  stroke-width: 8; /*  */
  fill: none;
  stroke-linecap: round; /*   */
  transform: rotate(-90deg); /*    */
  transform-origin: center;
}

/*    */
.circle-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 14px; /*   */
  font-weight: bold;
  color: #000000; /*   */
}

/*   */
.feature-name {
  font-size: 12px; /*   */
  font-weight: bold;
  color: #333;
  white-space: nowrap; /*    */
  overflow: hidden; /*    */
  text-overflow: ellipsis; /*  "..."    */
  max-width: 100%; /*   */
  cursor: help; /*   */
}
</style>



