<template>
  <div class="lime-chart-container">
    <!--   -->
    <div class="controls">
      <button @click="zoomIn" class="zoom-button">
        <i class="fas fa-search-plus"></i>
      </button>
      <button @click="zoomOut" class="zoom-button">
        <i class="fas fa-search-minus"></i>
      </button>
    </div>

    <!--  -->
    <div ref="chart" class="chart"></div>
  </div>
</template>

<script>
import * as echarts from "echarts";

export default {
  name: "LimeChart",
  props: {
    data: Object, //  JSON  -1500 
  },
  mounted() {
    this.initChart();
    window.addEventListener("resize", this.resizeChart); // 
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.resizeChart); //  
    this.destroyChart();
  },
  methods: {
    initChart() {
      if (!this.data || !this.$refs.chart) return;

      //    
      const sortedData = Object.entries(this.data)
        .sort(([, a], [, b]) => b - a);

      const categories = sortedData.map(([key]) => key); //  '
      const values = sortedData.map(([, value]) => value); //  

      this.chart = echarts.init(this.$refs.chart);

      const option = {
        tooltip: {
          trigger: "axis",
          axisPointer: { type: "shadow" },
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true,
        },
        xAxis: {
          type: "value",
          name: "  (LIME)",
          nameLocation: "middle",
          nameGap: 25,
        },
        yAxis: {
          type: "category",
          data: categories,
          name: "",
          inverse: true, //        
        },
        series: [
          {
            type: "bar",
            data: values,
            label: {
              show: true,
              position: "right", //     
              formatter: "{c}", //   
            },
            itemStyle: {
              color: "#3498db", //   
            },
          },
        ],
        dataZoom: [
          {
            type: "slider", //   
            yAxisIndex: 0, //  -Y
            start: 0, //     (-0%)
            end: 20, //    -20% 
            filterMode: "filter", //    
          },
          {
            type: "inside", //    
            yAxisIndex: 0,
            start: 0,
            end: 20,
          },
        ],
      };

      this.chart.setOption(option);
    },
    zoomIn() {
      const dataZoom = this.chart.getOption().dataZoom[0];
      let {start, end} = dataZoom;

      //     (Zoom In)
      const newStart = start + 5;
      const newEnd = end - 5;

      //    
      if (newEnd - newStart >= 10) {
        this.chart.dispatchAction({
          type: "dataZoom",
          start: newStart,
          end: newEnd,
        });
      }
    },
    zoomOut() {
      const dataZoom = this.chart.getOption().dataZoom[0];
      let {start, end} = dataZoom;

      //     (Zoom Out)
      const newStart = start - 5;
      const newEnd = end + 5;

      //    
      if (newStart >= 0 && newEnd <= 100) {
        this.chart.dispatchAction({
          type: "dataZoom",
          start: newStart,
          end: newEnd,
        });
      }
    },
    resizeChart() {
      if (this.chart) this.chart.resize(); //       
    },
    destroyChart() {
      if (this.chart) {
        this.chart.dispose(); //  
        this.chart = null;
      }
    },
  },
};
</script>

<style>
.lime-chart-container {
  position: relative;
  width: 100%;
  height: 100%;
}

.controls {
  position: absolute;
  top: 10px;
  right: 10px;
  display: flex;
  gap: 10px;
  z-index: 10;
}


.chart {
  width: 100%;
  height: 100%;
}
</style>
